import React, { Component } from 'react';
import uuid from 'uuid';
import $ from "jquery";
import Projects from './components/Projects';
import AddProject from './components/AddProject';
import './App.css';
import axios from 'axios';
import Tabletop from 'tabletop';

class App extends Component {
  constructor(){
    super();
    this.state = {
      data: [],
      projects: [],
      todos: []
    }
  }

  getTodos(){
    $.ajax({
      url: "https://sheets.googleapis.com/v4/spreadsheets/MIN3Oas7rzWoff82GiE5xAclnDHiouvYI/values:batchGet",
      
      dataType: 'json',
      cache: false,
      success: function(data){
        console.log(data);
        // this.setState({todos: data}, function(){
        //   console.log(this.state);
        // });
      }.bind(this),
      error: function(xhr, status, err){
        console.log(err);
      }
    });
  }

  getTodo(){
    axios.get(`https://sheets.googleapis.com/v4/spreadsheets/1waPRw7sSGcWwlG4dfQxVkH-irMvWdVkSVSWYXW_c-_s/values:batchGet`)
      .then(function (response) {
        console.log(response);
      });
  }

  getProjects(){
    this.setState({projects: [
      {
        id:uuid.v4(),
        title: 'adfa1',
        category: 'adf'
      },
      {
        id:uuid.v4(),
        title: 'adfa',
        category: 'adf1'
      }
    ]});
  }

  componentWillMount(){

  }

  componentDidMount(){
    Tabletop.init({
      key: '1waPRw7sSGcWwlG4dfQxVkH-irMvWdVkSVSWYXW_c-_s',
      callback: googleData => {
        this.setState({
          data: googleData
        })
      },
      simpleSheet: true
    })
  }

  handleAddProject(project){
    let projects = this.state.projects;
    projects.push(project);
    this.setState({projects:projects});
  }

  handleDeleteProject(id){
    let projects = this.state.projects;
    let index = projects.findIndex(x => x.id === id);
    projects.splice(index, 1);
    this.setState({projects:projects});
  }

  render() {
    /*return (
      <div className="App">
        <AddProject addProject={this.handleAddProject.bind(this)}/>
        <Projects projects={this.state.projects} onDelete={this.handleDeleteProject.bind(this)}/>
      </div>https://script.google.com/macros/s/AKfycbzuOXb05nYvdvo4gWRFY2odI0WYgZ0ui-eHeWtUtSE-/dev
    );*/
    const { data } = this.state
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title"></h1>
        </header>
				<table>
				  <tr>
				    <th>Category:</th>
				    <th>Title:</th> 
				    <th>Link:</th>
				  </tr>
				  </table>

        <div id="employee-details">
          {
            data.map(obj => {
              return (
                <div key={obj.employee}>
                <table>
				  <tr>
				    <td>{obj.employee}</td>
				    <td>{obj.favDog}</td>
				    <td><button><a href={obj.img} target="_blank">{obj.employee}</a></button></td>
				  </tr>
				</table>
                </div>
              )
            })
          }
        </div>
        <div>          
        <a href="https://script.google.com/macros/s/AKfycbzuOXb05nYvdvo4gWRFY2odI0WYgZ0ui-eHeWtUtSE-/dev" target="_blank">add new</a>
      </div>

      </div>
    );
  }
}

export default App;
